import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  base: '/web-tongqi/',
  routes: [
    {
		path: '/',
		redirect: '/Home'
	},
    {
      path: '/',
      	component: resolve => require(['@/components/ContentPage'], resolve),
     	 children: [{
              path: '/Home',
              name: 'Home',
              component: resolve => require(['@/pages/Home'], resolve)
          }
      ]
  },
  ]
})



